//////\\\\\\
//font-size
//////\\\\\\
.fs-xl {
  font-size: 3.8rem;
}

.fs-xlm {
  font-size: 3.5rem;
}

.fs-xls {
  font-size: 2.65rem;
}
.fs-l {
  // font-size: 1.65rem;
  font-size: 1.55rem;

}

.fs-team-title {
  font-size: 2rem;
}

.fs-m {
  font-size: 1.2rem
}

.fs-ms {
  font-size: 1.2em
}

.fs-s {
  font-size: 1.05rem;
  font-weight: 400 !important;

}

.fs-xs {
  font-size: 0.95rem;
  font-weight: 400 !important;
}

.fs-xxs {
  font-size: 0.87rem;
}


@media screen  and (max-width:1400px){
  .fs-xl {
    font-size: 3rem;
  }
  .fs-l {
    // font-size: 1.65rem;
    font-size: 1.25rem;
  
  }
}
@media screen  and (max-width:600px){
  .fs-xl {
    font-size: 3rem;
  }
  .fs-xlm {
    font-size: 2.8rem;
  }
  .fs-l {
    font-size: 1.45rem;
  }
  .fs-xls {
    font-size: 2.45rem;
  }
  .fs-m {
    font-size: 1.1rem
  }
}
@media screen  and (max-width:500px){
  .fs-xl {
    font-size: 2.5rem;
  }
  .fs-xlm {
    font-size: 2.3rem;
  }
  .fs-xs{
    font-size: 0.85rem;
  }

  .fs-l {
    font-size: 1.17rem;
  }
  .fs-xls {
    font-size: 1.96rem;
  }
  .fs-s{
    font-size: 0.87rem;
  }
  .fs-m {
    font-size: 1rem
  }
}
