.app-bg {
  // top:0;
  position: fixed;
  z-index: 0;
  width: 100vw;
  height: 100vh;
  background-image: url(./assets/images/main.webp);
  background-position: center top;
  background-size: cover;


  transform: scale3d(1.7,1.7,1.7);
}

