.indented-section{
  display: flex;
  :nth-child(1){
      margin-right: 5px;
  }
}
.verified-contract-address{
  max-width: 90vw;
  position: relative;
  display: flex;
  margin: auto;
  width: fit-content;
  margin-top: 8rem;
  text-align: center;
  button{
    text-decoration: underline;
  }
  @media screen and (max-width:1100px){
    flex-direction: column;
  }
  @media screen and (max-width:500px){

    margin-top: 5rem;
  }
}
