.welcome-page{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}
.welcome-logo{
    position: absolute;
    height: 120px;
    left:0;
    right:0;
    margin: auto;
}

@keyframes welcome-animation {
    0%{
        opacity: 0;
        top:200px
    }
    100%{
        opacity: 1;
        top:0;
    }
}
.welcome-page-inner{
    position: relative;
    max-width: 100vw;
    width:  calc(100vh / 0.5625);
    margin:auto;
    animation: welcome-animation 550ms ease-in-out;
}
.welcome-page-img-wrapper{
    position: relative;
    width:  calc(100vh / 0.5625);
    height: 100vh ;
    max-width: 100vw;
}
.welcome-img{
    height: 100%;
    width: 100%;
}
.welcome-video{
    height: 100%;
    width: 100%;
}

.welcome-page-enter-wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    position: absolute;
    bottom:5vh;
    left:0;
    right: 0;
    margin: auto;
    width:360px;
    height: calc(360px * 0.7115);
    // padding: 2rem;
    box-sizing: border-box;
    // background-color: rgba(0, 0, 0, 0.6);
    border-radius: 15px;
    p{
        font-size: 1.8rem;
        z-index: 1;
    }
    animation: welcome-animation 2500ms linear infinite;

}
@keyframes welcome-animation {
    0%{
        box-shadow:0px 0px 13px 2px #ed07c1;
    }
    50%{
        box-shadow:0px 0px 10px 2px rgb(81, 199, 241);
    }
    100%{
        box-shadow:0px 0px 13px 2px #ed07c1;
    }
}

.welcome-page-enter-wrapper-background{
    top:0px;
    position: absolute;

    width:360px;
    height: calc(360px * 0.7115);
    // background-color: blue;
    background-color: rgba(0, 0, 0, 0.3);

    border-radius: 15px;
      
}
.welcome-page-border{
    position: absolute;
    mix-blend-mode:screen;

    height: calc(360px * 0.7115);
    width:360px;
}

.welcome-btn{
    position: relative;
    // margin: 2rem 0 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 300px;
    height: 55px;

    border-radius: 10px;
    color:black;
    cursor: pointer;
    background-color: rgb(81, 199, 241);
    animation: btn-animation 2300ms linear infinite;
    // transition:background-color 200ms ease-in-out, color 200ms ease-in-out;

    &:hover{
        background-color: white  !important;
     
    }
}

