.welcome-social-wrapper{
    display: flex;
    align-items: center;
    // background-color: red;
    margin: 0.1rem 0 0.5rem 0;
    z-index: 1;
}
.welcome-social-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 28px;
    width: 28px;
    margin: 0.5rem;
    cursor: pointer;
    // img{
    //     // height: 90%;
    // }
    border-radius: 50%;
    // background-color: white;
}



.welcome-social-wrapper-border{
    width: 67px;
    height: 2px;
    background-color: white;
    margin: 1rem 0  1rem 0;
    animation: welcome-animation 2500ms linear infinite;

}

.welcome-page-bottom-animation{
    display: none;
    position: absolute;
    width: 170vw;
    height: 100px;
    bottom:-20px;
}

.top-section-audio{
    position: absolute;
    bottom: 10px;
    right: 6px;
    cursor: pointer;
    z-index: 10;
}
#top-section-playing{
    height: 35px !important;
}
@media screen and (max-width:calc(100vh / 0.667)){
    .welcome-page-inner{
        height: calc(100vw * 0.667);
    }
  
    .welcome-img{
        height: calc(100vw * 0.667);
        width: unset;
    }
}
@media screen and (max-width:700px){
    // .welcome-page-img-wrapper{
    //     margin-top: 3rem;
    // }
    .welcome-page-enter-wrapper{
        position: relative;
        bottom:unset;
        top:1.1rem;
    }

    .welcome-page-enter-wrapper{
        
        p{
            font-size: 1.9rem;
        }
    }

    .welcome-page-bottom-animation{
        display: flex;
       
    }
    .welcome-page-enter-wrapper{
  
        p{
            font-size: 1.3rem;
        }
    
    }
    .top-section-audio{
    
        bottom: 85px;
        
    }
    .welcome-page{
        width: 100vw;
        height: 100vh;
        overflow-y: scroll;
    }
}