$blue:rgb(81, 199, 241);


.top-section{
    position: relative;
    width: 100vw;
    height: 100vh;
    z-index: 5  ;

}
.top-section-inner{
    // background-color: red;
    width: 1155px;
    margin: auto;
    max-width: 90vw;
}

.top-section-bottom{
    display: flex;
    margin-top: 1rem;
}
.top-section-info-wrapper{
    // display: flex;
    // flex-direction: column;
    margin: 1rem 0 0 1.5rem;
    
}
.top-section-info{
    margin-top: 1.3rem;
    margin-bottom: 1.5rem;
    text-align: justify;
}
.top-section-encourage{
    margin-top: 0.5rem;
}
.top-section-logo{
    position: relative;
    left:-46px;
    width: 250px;
    visibility: hidden;
}
.top-section-img-wrapper{
    position: relative;
    flex-shrink: 0;
    height: 550px;
    width: 490px;
