.top-sectin-social-wrapper{
    display: flex;
    margin-top: 1.8rem
}
.social-btn{
 
    margin-right: 1rem;
}



@media screen and (max-height:720px) and (min-width:1100px){
    .top-section-logo{
        left:-17px;
        width: 170px;
    }
    .top-section-img-wrapper{
        height: 460px;
        width: 409px;
    }
    .top-section-info-wrapper{
        margin: -1rem 0 0 1.5rem;
    }
}

@media screen  and (max-width:1130px){
    .top-section-img-wrapper{
        height: 480px;
        width: 422px;
    }
    .top-section-info-wrapper{
        margin: -1rem 0 0 1.5rem;
    }
    .top-section-logo{
        left:-17px;
        width: 200px;
    }
}

@media screen  and (max-width:1020px){
    .top-section{
        height: fit-content;
    }
    .top-section-inner{
        width: 705px;
    }
    .top-section-img-wrapper{
        height: 801px;
        width: 705px;
    }
    .top-section-bottom{
        flex-direction: column-reverse;
        // background-color: red;
    }
    .top-section-info-wrapper{
        margin: 2rem 0 3rem 0rem;
    }
    .top-section-logo{
        left:unset;
        width: 200px;
    }
    .top-section-info{
        margin-top: 1.5rem;
    }
}

@media screen  and (max-width:785px){
  
    .top-section-img-wrapper{
        height: calc(90vw / 0.879);
        width: 90vw;
    }

}
@media screen  and (max-width:525px){
    // .top-section-bottom{
    //     flex-direction: column;
    //     // background-color: red;
    // }
    .top-section-info-wrapper{
        margin: -1rem 0 3rem 0rem;
    }
    .top-section-logo{
        margin-top:-10px
    }
    .top-section-img-wrapper{
        display: none;
    }
    .top-section-info-wrapper{
        display: flex;
        flex-direction: column;
        justify-content: center;

        //calculation for mobile height- logo height - logo margin top
        height: calc(100vh -  132px);
       
    }
