        margin-top: 1rem;
    }
    .top-section-encourage{
        margin-top: 1rem;
    }
    // .top-section-btn-wrapper{
    //     margin-top: 0.5rem;
    //     flex-direction: column;
    // }
    // .top-section-connect-btn{
       
    //     width: 100%;
    //     height: 42px;
    //     margin-top: 0.5rem;
    // }
    .top-sectin-social-wrapper{
        margin-top: 1rem
    }
    .social-btn{
        margin-right: 0.7rem;
    }
}